/* CSS for button styling*/
.display-b {
  margin-right: 100px;
  align-items: center;
  background-color: rgb(230, 204, 169);
  border: 2px solid #111;
  border-radius: 8px;
  box-sizing: border-box;
  color: #111;
  cursor: pointer;
  display: flex;
  font-family: Inter,sans-serif;
  font-size: 16px;
  height: 45px;
  justify-content: center;
  line-height: 24px;
  max-width: 100%;
  padding: 0 25px;
  position: relative;
  text-align: center;
  text-decoration: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;

}

.display-b:after {
  background-color: #111;
  border-radius: 8px;
  content: "";
  display: block;
  height: 45px;
  left: 0;
  width: 100%;
  position: absolute;
  top: -2px;
  transform: translate(8px, 8px);
  transition: transform .2s ease-out;
  z-index: -1;
}

.display-b:hover:after {
  transform: translate(0, 0);
}

.display-b:active {
  background-color: #ffdeda;
  outline: 0;
}

.display-b:hover {
  outline: 0;
}

@media (min-width: 768px) {
  .display-b {
    padding: 0 40px;
  }
}

